import baseURL from '@/config'
export default {
	onLoad () {
		this.initVideo()
	},
	data () {
		return {
			videoSrc: ''
		}
	},
	methods: {
		async initVideo () {
			let res = await this.$fetch(this.$api.getVideo)
	
			 this.videoSrc = baseURL.slice(0, baseURL.length - 1) +  res.data
			 console.log(this.videoSrc)
		}
	}
}
